import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';
import React from 'react';

import SitePage from 'components/SitePage';
import WritingIcon from 'components/WritingIcon';

import papers from './papers';
import Poem from './Poem';
import poems from './poems';
import ResearchPaper from './ResearchPaper';
import styles from './Writing.css';

const TITLE = `Writing - Cedric McDougal`;
const DESCRIPTION = `Academic papers and poems I have written.`;

const Writing = () => {
  const classes = makeStyles(styles)();

  return (
    <SitePage
      description={DESCRIPTION}
      favicon="writing"
      icon={WritingIcon}
      mobileTitle="Writing"
      title={TITLE}>
      <div className={classes.container}>
        <Typography className={classes.sectionTitle} variant="h5">
          Papers
        </Typography>
        <div>
          {papers.map(({ abstract, href, image, imageTitle, title }) => {
            return (
              <div key={title} className={classes.researchPaperContainer}>
                <ResearchPaper
                  abstract={abstract}
                  href={href}
                  image={image}
                  imageTitle={imageTitle}
                  title={title}
                />
              </div>
            );
          })}
        </div>
        <Typography className={classes.sectionTitle} variant="h5">
          Poems
        </Typography>
        <div>
          {poems.map(({ body, title }) => {
            return (
              <div key={title} className={classes.poemContainer}>
                <Poem body={body} title={title} />
              </div>
            );
          })}
        </div>
      </div>
    </SitePage>
  );
};

Writing.propTypes = {};

export default Writing;
